

















































.city-category-wrapper {
    padding: 30px 0;

    .alphabet {
        display: flex;
        justify-items: center;
        margin-bottom: 30px;
        dt {
            font-size: 16px;
        }

        dd {
            font-size: 14px;
            margin: 0 10px;

            a {
                display: inline-block;
                width: 25px;
                height: 25px;
                line-height: 25px;
                text-align: center;
                color: #666666;

                &:hover {
                    background-color: #F8F8F8;
                    border-radius: 50%;
                }
            }
        }
    }

    .letter-data {

        padding: 13px 30px 13px 10px;
        border-radius: 10px;

        &:hover {
            background-color: #F8F8F8;
        }

        h4 {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: #13D1BE;
            line-height: 40px;
            text-align: center;
            font-size: 16px;
            font-weight: 500;
            color: #ffffff;
        }

        .city {

            display: inline-block;
            margin: 10px 20px;

            color: #666666;
            font-size: 14px;

            &:hover {
                color: #13D1BE;
            }
        }

    }

}

