








































































































.page-change-city {
    width: 100%;
    box-sizing: border-box;
    background-color: #fff;
    border: 1px solid #E5E5E5;
    border-radius: 4px;

    margin-top: 20px;
    padding: 20px;

    > .el-row:not(:last-of-type) {
        padding-bottom: 20px;
        border-bottom: 1px solid #E5E5E5;;
    }

    .el-select {
        margin-left: 10px;
    }
}
